<template>
  <el-header
    class="header-flex"
    :style="{width: (getCollapseState == false)?'calc(100% - 200px)':'calc(100% - 64px)',
                 left: (getCollapseState == true)?'64px':'',
                 backgroundColor: getHeaderBColor,
                 color: getHeaderColor}"
  >
    <!---->
    <div float="left" @click="isOpen">
      <p multiBtn>
        <i
          :class="{'el-icon-s-fold': getCollapseState == false?true:false,
          'el-icon-s-unfold': getCollapseState == true?true:false}"
        ></i>
      </p>
    </div>
    <!--  -->
    <div float="right">
      <!---->
      <el-popover placement="top-start" title="" width="200" trigger="click">
        <el-divider></el-divider>
        <div>
          <p>
            <i class="el-icon-success" sColor></i>
            <a href="#"></a>
          </p>
          <p>
            <i class="el-icon-warning" wColor></i>
          </p>
        </div>
        <el-divider></el-divider>
        <div class="btn-ground">
          <el-button type="primary" size="mini"></el-button>
          <el-button type="danger" size="mini"></el-button>
        </div>
        <p slot="reference" multiBtn @click="MQBox">
          <el-tooltip class="item" effect="dark" content="" placement="bottom">
            <i class="el-icon-message-solid badge"></i>
          </el-tooltip>
        </p>
      </el-popover>

      <!---->
      <el-tooltip class="item" effect="dark" content="" placement="bottom">
        <p multiBtn @click="$router.go(0)">
          <i class="el-icon-refresh"></i>
        </p>
      </el-tooltip>
      <!---->
      <el-dropdown trigger="hover" placement="bottom" @command="handleCommand">
        <p class="el-dropdown-link" multiBtn>
          <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
        </p>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="profile"></el-dropdown-item>
          <el-dropdown-item command="updatePass"></el-dropdown-item>
          <el-dropdown-item command="logout"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <!---->
      <el-tooltip class="item" effect="dark" content="" placement="bottom">
        <p multiBtn rotate @click="openGlobalSetting">
          <i class="el-icon-more"></i>
        </p>
      </el-tooltip>
    </div>
  </el-header>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "header",
  data() {
    return {
      aa: false
    };
  },
  computed: {
    ...mapGetters({
      getCollapseState: "CollapseState", //
      getHeaderBColor: "HeaderBColor", //
      getHeaderColor: "HeaderColor" //
    })
  },
  methods: {
    isOpen() {
      //
      if (this.getCollapseState == false) {
        this.$store.commit("setCollapseState", true);
      } else {
        this.$store.commit("setCollapseState", false);
      }
    },
    MQBox() {},
    //
    handleCommand(command) {
      if (command === "profile") {
        this.$router.push({
          path: "/profile"
        });
      } else if (command === "updatePass") {
        this.$router.push({
          path: "/updatePass"
        });
      } else if (command == "logout") {
        this.$router.push({
          path: "/login"
        });
      }
    },
    openGlobalSetting() {
      // this.$store.commit('globalSetting/setDrawerState', true)
    }
  }
};
</script>

<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

p[multiBtn] {
  width: 38px;
  max-width: 38px;
  height: 38px;
  max-height: 38px;
  font-size: 18px;
  border-radius: 38px;
  margin: 2px;
  cursor: pointer;
  display: inline-flex;
  vertical-align: middle;
}

[multiBtn] i {
  line-height: 38px;
  margin: 0 auto;
  font-size: 30px;
}

[multiBtn]:hover {
  box-shadow: 0 0 6px #b3b3b3 inset;
  transition: all 0.5s ease;
}

[rotate] i {
  transform: rotate(90deg);
}

[sColor] {
  color: #67c23a;
  margin-right: 2px;
}

[wColor] {
  color: #e6a23c;
  margin-right: 2px;
}

[float="left"] {
  position: absolute;
  left: 0;
  cursor: pointer;
}

[float="right"] {
  position: absolute;
  right: 20px;
  cursor: pointer;
}

.badge:after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  /*margin-top: 0px;*/
  background-color: #e60000;
  border-radius: 10px;
}
</style>