<template>
  <div class="titleNavBox" :style="{width: (getCollapseState == false)?'calc(100% - 200px)':'calc(100% - 64px)'}">
    <div style="position: relative">
      <i v-if="isShow" leftIcon class="el-icon-d-arrow-left scroll" @click="toLeft"></i>
      <div class="titleNav" @click.right.prevent="mouseRight">
        <el-tag v-for="(item, index) in getAsideTitle"
                :key="index"
                ref="tag"
                :active-index="item.activeIndex"
                :class="{title: true, active: (active === item.activeIndex)?true:false, marginR: (index != getAsideTitle.length - 1)}"
                @click="toUrl(item.path, item.activeIndex)"
                @close="closeTag(item.title, item.activeIndex)"
                :closable="(item.title == '')? false : true">
          {{item.title}}
        </el-tag>
      </div>
      <i v-if="isShow" rightIcon class="el-icon-d-arrow-right scroll" @click="toRight"></i>
      <!-- <component v-if="mouseR" v-bind:is="RightFun" :clientX="clientX" :clientY="clientY" @close-r="closeRightF"></component> -->
    </div>
  </div>
</template>

<script>
  // import RightFun from "../../../components/RightFun";
  import {mapGetters} from 'vuex'

  export default {
    name: "AsideTitle",
    data() {
      return {
        navWidth: '', //
        pageWidth: '', //
        isShow: false,
        mouseR: false, //
        clientX: '',
        clientY: ''
      }
    },
    computed: {
      ...mapGetters({
        getAsideTitle: 'AsideTitle',
        active: 'active',
        getCollapseState: 'CollapseState',
        getCrumbs: 'Crumbs',
        getNavs: 'Navs'
      })
    },
    methods: {
      /*
      * 
      *   path: 
      *   index:
      * */
      toUrl(path, index) {
        //
        this.$store.commit('setActiveIndex', index)
        this.$router.push({
          path: path
        })

        //vuex/header/crumbs
        let parentTitle
        let childTitle
        // let crumbs = new Array()
        const nav = this.getNavs.nav
        for (let i = 0; i < nav.length; i++) {
          parentTitle = nav[i].title
          if(nav[i].child) {
            for (let j = 0; j < nav[i].child.length; j++) {
              if(nav[i].child[j].path == path) {
                childTitle = nav[i].child[j].title
                // crumbs.push({
                //   parentTitle: parentTitle,
                //   childTitle: childTitle
                // })
                this.setCrumbs(parentTitle,childTitle)
                // this.$store.commit('header/setCrumbs', crumbs)
                return;
              }
            }
          } else {
            parentTitle = nav[i].title
            // crumbs.push({
            //   parentTitle: parentTitle,
            //   childTitle: ''
            // })
            this.setCrumbs(parentTitle,childTitle)
            // this.$store.commit('header/setCrumbs', crumbs)
            return;
          }
        }
      },
      setCrumbs(pt,ct) {
        let crumbs = new Array()
        // crumbs.push({
        //   'parentTitle': pt,
        //   'childTitle': ct
        // })
        crumbs.push(pt,ct)
        this.$store.commit('setCrumbs', crumbs)
      },
      /*
    * 
    * title: 
    * index: 
    * */
      closeTag(title, index) {
        /*
        *1.
        * 
        * 
        * 
        * */
        if (index === this.active) {

          for (let i = 0; i < this.getAsideTitle.length; i++) {


            if (this.getAsideTitle[i].activeIndex == index) {
              //
              let nextIndex = this.getAsideTitle[i + 1] || this.getAsideTitle[i - 1]
              this.$store.commit('setActiveIndex', nextIndex.activeIndex)
              //
              const activePath = nextIndex.path
              this.$router.push({
                path: activePath
              })
            }
          }
        }
        /*
        * 
        * */
        let result = this.getAsideTitle.filter(item => {
          return item.title != title
        })
        this.$store.commit('delOrAddAside', result)
      },
      /*
      * 
      * */
      toLeft() {
        this.$nextTick(function () {
          this.move("left")
        })
      },
      /*
      * 
      * */
      toRight() {
        this.$nextTick(function () {
          this.move("right")
        })
      },
      /*
      *   - 
      * */
      showBtn() {
        this.$nextTick(function () {
          //-----
          if (this.getCollapseState == false) {
            this.pageWidth = document.documentElement.clientWidth - 200
          } else {
            this.pageWidth = document.documentElement.clientWidth - 64
          }
          //
          this.navWidth = this.$el.querySelector('.titleNav').scrollWidth

          //  
          if (this.navWidth >= this.pageWidth) {
            this.isShow = true
            this.$el.querySelector('.titleNav').style.right = '20px'
          } else {
            this.isShow = false
          }
        })
      },
      /*
      * ,
      * */
      move(direction) {
        if (direction === 'left') {
          let num = 0
          num += (this.navWidth - this.pageWidth + 40) / 3
          this.$el.querySelector('.titleNav').scrollLeft -= num
        } else {
          let num = 0
          num += (this.navWidth - this.pageWidth + 40) / 3
          this.$el.querySelector('.titleNav').scrollLeft += num
        }
      },
      /*
      * 
      * */
      mouseRight(event) {
        //
        this.mouseR = true
        //X, y 
        this.clientX = event.clientX
        this.clientY = event.clientY
      },
      //
      closeRightF(state) {
        this.mouseR = state
      }
    },
    mounted() {
      /**
       * vuex
       * */
      let _this = this
      window.addEventListener('beforeunload', e => {
        sessionStorage.setItem("titleItem", JSON.stringify({
          titleItem: _this.getAsideTitle,
          activeIndex: _this.active,
          crumbs: _this.getCrumbs
        }))
      })
      /*
      * 
      * */
      window.addEventListener('resize', function() {
        _this.showBtn()
      })
      /*
      * mouseR()
      * */
      window.addEventListener('click', function() {
        _this.mouseR = false
      })
    },
    updated() {
      this.showBtn()
    },
    created() {
      this.showBtn()
      //sessionvuex
      let loadBeforeRes = JSON.parse(sessionStorage.getItem("titleItem"))
      if (loadBeforeRes != null) {
        if (loadBeforeRes.titleItem != null && (loadBeforeRes.activeIndex != null || loadBeforeRes.activeIndex != '')) {
          this.$store.commit('delOrAddAside', loadBeforeRes.titleItem)
          //sessionactiveIndexcount
          const maxResult = this.getAsideTitle.reduce((pre, las) => pre.id > las.id ? pre : las)
          this.$store.commit('setActiveIndex', loadBeforeRes.activeIndex)
          //0
          this.$store.commit('setCount', maxResult.activeIndex)
          this.$store.commit('setCrumbs', loadBeforeRes.crumbs)
        }
      }
    },
    components: {
      // RightFun
    }
  }
</script>


<style scoped>

  .titleNav {
    position: absolute;
    left: 0;
    right: 0;
    height: 42px;
    max-height: 42px;
    line-height: 42px;
    margin-bottom: 10px;
    margin-left: 20px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    background-color: #FFFFFF;
    transition: all 3s ease-in-out;
  }

  .titleNav::-webkit-scrollbar {
    display: none;
  }

  .title {
    position: relative;
    cursor: pointer;
    background-color: #d9d9d9;
    color: #4d4d4d;
    border: 0px;
    padding-left: 15px;
    font-size: 14px;
    box-shadow: 1px 1px 1px 1px #999999;
  }

  .active:before {
    content: ' ';
    width: 8px;
    height: 8px;
    background-color: #00cc00;
    /*opacity: 0.5;*/
    border-radius: 5px;
    z-index: 1000;
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
  }

  .active {
    background-color: #5F4B8B;
    color: #ffffff;
  }

  .marginR {
    margin-right: 6px;
  }

  [leftIcon], [rightIcon] {
    position: absolute;
    cursor: pointer;
    height: 42px;
    line-height: 42px;
    margin-top: -5px;
    z-index: 500;
  }

  [leftIcon] {
    left: 0;
    top: 6px;
    box-shadow: 2px 0 5px 0 #bfbfbf;
  }

  [rightIcon] {
    right: 0;
    top: 6px;
    box-shadow: 0 2px 5px 0 #bfbfbf;
  }

  .titleNavBox {
    position: fixed;
    top: 60px;
    right: 0;
    text-align: left;
    height: 42px;
    z-index: 200;
    box-shadow: 0 1px 3px 0 #595959;
  }

</style>